@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir-Heavy;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

body {
  --font-color: #3d4e61;
  --border-color: #e1e7ed;
  color: var(--font-color);
  font-family: Avenir, sans-serif;
}

.field {
  box-sizing: border-box;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  align-items: center;
  margin-top: 20px;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.field__big {
  width: 100%;
  height: 70px;
}

.field__small {
  width: 100%;
  height: 42px;
}

.field__icon {
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: cover;
  min-width: 19px;
  min-height: 19px;
  margin-left: 26px;
  margin-right: 17px;
}

.field__icon--small {
  min-width: 11px;
  min-height: 11px;
  margin-left: 13px;
  margin-right: 9px;
}

.field__input {
  background: none;
  border: none;
  outline: none;
  width: 100%;
  height: 19px;
  padding: 0;
  font-family: Avenir, sans-serif;
  font-size: 16px;
  font-weight: 400;
}

.field__input--small {
  font-size: 14px;
}

.field:hover {
  font-weight: 400;
  box-shadow: 0 3px 8px #3d4e6133;
}

.field__input:focus {
  font-family: Avenir-Heavy, sans-serif;
  font-weight: 900;
}

.field:focus-within {
  background-image: linear-gradient(#fff, #f6f6f7);
  font-weight: bold;
  box-shadow: 0 4px 4px #00000040;
}

.field__input::placeholder {
  color: var(--font-color);
  font-family: inherit;
  font-weight: 300;
}

.field__input--small::placeholder {
  font-size: 14px;
}
